import { create } from 'zustand';
import { supabase } from '../lib/supabase';

interface EssayState {
  loading: boolean;
  error: string | null;
  startWriting: () => Promise<void>;
  setupRealtimeSubscriptions: () => Promise<(() => void) | undefined>;
}

export const useEssayStore = create<EssayState>((set, get) => ({
  loading: false,
  error: null,

  setupRealtimeSubscriptions: async () => {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) return;

    // Subscribe to essay changes
    const essaysSubscription = supabase
      .channel('essays-changes')
      .on('postgres_changes', 
        { 
          event: '*', 
          schema: 'public', 
          table: 'essays',
          filter: `user_id=eq.${user.id}`
        }, 
        () => {
          console.log('📝 Essay change detected...');
          // You can add any necessary updates here
        }
      )
      .subscribe();

    // Store cleanup function in a variable
    const cleanup = () => {
      essaysSubscription.unsubscribe();
    };

    // Add cleanup to window unload
    window.addEventListener('unload', cleanup);

    // Return cleanup function for component unmount
    return cleanup;
  },

  startWriting: async () => {
    set({ loading: true, error: null });
    try {
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      if (authError) {
        console.error('Auth error:', authError);
        throw new Error('Authentication error: ' + authError.message);
      }
      if (!user) throw new Error('No authenticated user found');

      const { data: skills, error: skillsError } = await supabase
        .from('skills')
        .select('*')
        .eq('status', 'completed')
        .eq('user_id', user.id);

      if (skillsError) {
        console.error('Error fetching skills:', skillsError);
        throw new Error('Failed to fetch skills: ' + skillsError.message);
      }

      const response = await fetch(`${import.meta.env.VITE_SUPABASE_URL}/functions/v1/generate-essay`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${import.meta.env.VITE_SUPABASE_ANON_KEY}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          skills,
          experiences: [] // You would populate this with actual experiences
        }),
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.message || 'Failed to generate essay');
      }

      const { essay } = await response.json();

      // Save the generated essay
      const { error: saveError } = await supabase.from('essays').insert({
        user_id: user.id,
        title: 'AI Generated Essay',
        content: essay,
        ai_generated: true,
      });

      if (saveError) {
        console.error('Error saving essay:', saveError);
        throw new Error('Failed to save essay: ' + saveError.message);
      }

    } catch (error: any) {
      console.error('Error in startWriting:', {
        error,
        message: error.message,
        details: error.details,
        hint: error.hint
      });
      set({ error: `Failed to generate essay: ${error.message || 'Unknown error'}` });
      throw error;
    } finally {
      set({ loading: false });
    }
  },
}));