import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export interface Attachment {
  id: string;
  name: string;
  type: string;
  size: number;
  url: string;
}

export interface LocalDocument {
  id: string;
  name: string;
  content: string;
  category: string;
  size: string;
  created_at: string;
  attachments?: Attachment[];
}

interface LocalDocumentState {
  documents: LocalDocument[];
  loading: boolean;
  error: string | null;
  addDocument: (name: string, content: string, category: string) => void;
  getDocuments: () => LocalDocument[];
  getDocument: (id: string) => LocalDocument | undefined;
  updateDocument: (id: string, updates: Partial<LocalDocument>) => void;
  deleteDocument: (id: string) => void;
  clearError: () => void;
}

export const useLocalDocumentStore = create<LocalDocumentState>()(
  persist(
    (set, get) => ({
      documents: [],
      loading: false,
      error: null,

      clearError: () => set({ error: null }),

      addDocument: (name: string, content: string, category: string) => {
        try {
          if (!name.trim()) {
            throw new Error('Document name is required');
          }
          if (!content.trim()) {
            throw new Error('Document content is required');
          }
          if (!category.trim()) {
            throw new Error('Document category is required');
          }

          const newDocument: LocalDocument = {
            id: Date.now().toString(), // Use timestamp as ID
            name: name.trim(),
            content: content.trim(),
            category: category.trim(),
            size: `${(content.length / 1024).toFixed(1)} KB`,
            created_at: new Date().toISOString(),
            attachments: [],
          };

          set(state => ({
            documents: [newDocument, ...state.documents],
            error: null
          }));
        } catch (error: any) {
          set({ error: error.message || 'Failed to add document' });
          throw error;
        }
      },

      getDocuments: () => {
        return get().documents;
      },

      getDocument: (id: string) => {
        if (!id) {
          set({ error: 'Document ID is required' });
          return undefined;
        }
        return get().documents.find(doc => doc.id === id);
      },

      updateDocument: (id: string, updates: Partial<LocalDocument>) => {
        try {
          if (!id) {
            throw new Error('Document ID is required');
          }

          const document = get().getDocument(id);
          if (!document) {
            throw new Error('Document not found');
          }

          // Validate updates
          if (updates.name && !updates.name.trim()) {
            throw new Error('Document name cannot be empty');
          }
          if (updates.content && !updates.content.trim()) {
            throw new Error('Document content cannot be empty');
          }
          if (updates.category && !updates.category.trim()) {
            throw new Error('Document category cannot be empty');
          }

          set(state => ({
            documents: state.documents.map(doc => {
              if (doc.id === id) {
                const updatedDoc = { 
                  ...doc, 
                  ...updates,
                  name: updates.name ? updates.name.trim() : doc.name,
                  content: updates.content ? updates.content.trim() : doc.content,
                  category: updates.category ? updates.category.trim() : doc.category
                };
                // Update size if content changed
                if (updates.content) {
                  updatedDoc.size = `${(updates.content.length / 1024).toFixed(1)} KB`;
                }
                return updatedDoc;
              }
              return doc;
            }),
            error: null
          }));
        } catch (error: any) {
          set({ error: error.message || 'Failed to update document' });
          throw error;
        }
      },

      deleteDocument: (id: string) => {
        try {
          if (!id) {
            throw new Error('Document ID is required');
          }

          const document = get().getDocument(id);
          if (!document) {
            throw new Error('Document not found');
          }

          set(state => ({
            documents: state.documents.filter(doc => doc.id !== id),
            error: null
          }));
        } catch (error: any) {
          set({ error: error.message || 'Failed to delete document' });
          throw error;
        }
      },
    }),
    {
      name: 'local-documents-storage', // unique name for localStorage
    }
  )
); 