import { create } from 'zustand';
import { supabase } from '../lib/supabase';
import debounce from 'lodash/debounce';

interface SearchResult {
  id: string;
  type: 'document' | 'sao' | 'skill';
  title: string;
  description?: string;
  category?: string;
}

interface SearchStore {
  results: SearchResult[];
  loading: boolean;
  error: string | null;
  search: (query: string) => Promise<void>;
  clearResults: () => void;
  clearError: () => void;
}

const debouncedSearch = debounce(async (query: string, set: any) => {
  if (!query.trim()) {
    set({ results: [], loading: false, error: null });
    return Promise.resolve();
  }

  set({ loading: true, error: null });

  try {
    const { data: { user }, error: authError } = await supabase.auth.getUser();
    if (authError) {
      console.error('Auth error:', authError);
      throw new Error('Authentication error: ' + authError.message);
    }
    if (!user) throw new Error('No authenticated user found');

    // Single query using UNION to search across all tables
    const { data, error: searchError } = await supabase.rpc('search_all', {
      search_query: query.trim(),
      user_id: user.id
    });

    if (searchError) {
      console.error('Search error:', searchError);
      throw new Error('Search failed: ' + searchError.message);
    }

    set({ 
      results: data || [], 
      loading: false,
      error: null
    });
    return Promise.resolve();
  } catch (error: any) {
    console.error('Search error:', {
      error,
      message: error.message,
      details: error.details,
      hint: error.hint
    });
    set({ 
      error: `Search failed: ${error.message || 'Unknown error'}`, 
      loading: false,
      results: []
    });
    return Promise.reject(error);
  }
}, 300); // 300ms debounce

export const useSearchStore = create<SearchStore>((set) => ({
  results: [],
  loading: false,
  error: null,

  clearError: () => set({ error: null }),

  search: (query: string) => {
    return debouncedSearch(query, set) || Promise.resolve();
  },

  clearResults: () => set({ results: [], error: null })
})); 